@media screen and (max-width: 768px) {
  .container {
    max-width: 360px;
    padding: 0 20px;
  }
  .header {
    display: none;
  }
  .header-mob {
    height: 57px;
    display: block;
    position: relative;
    padding-top: 29px;
  }
  .header-mob__wrapper {
    margin-top: 46px;
    background: #f6f6f6;
    padding: 50px 0;
  }
  .header-mob__list {
    display: flex;
    gap: 60px;
  }
  .header-mob__list a {
    font-size: 20px;
    line-height: 120%;
    font-weight: 300;
  }
  .header-mob__images {
    margin-top: 56px;
    display: flex;
    max-width: 193px;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 50px;
  }
  .header-mob .container {
    position: relative;
  }
  .header-content {
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;
  }
  /* Стили логотипа */
  .header-logo {
    width: 68px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1002;
  }
  /* Стили кнопки меню */
  .menu-toggle {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 1001;
    width: 22px;
    height: 20px;
    position: relative;
    margin-right: auto;
  }
  /* Стили бургер-иконки */
  .burger-icon {
    display: block;
    width: 22px;
    height: 2px;
    background-color: #000;
    position: relative;
  }
  .burger-icon::before,
  .burger-icon::after {
    content: "";
    position: absolute;
    width: 22px;
    height: 2px;
    background-color: #000;
    left: 0;
  }
  .burger-icon::before {
    top: -6px;
  }
  .burger-icon::after {
    bottom: -6px;
  }
  /* Стили крестика */
  .close-icon {
    display: none;
    width: 21px;
    height: 21px;
    position: relative;
  }
  .close-icon::before,
  .close-icon::after {
    content: "";
    position: absolute;
    width: 21px;
    height: 2px;
    background-color: #000;
    top: 50%;
    left: 0;
    transform-origin: center;
  }
  .close-icon::before {
    transform: rotate(45deg);
  }
  .close-icon::after {
    transform: rotate(-45deg);
  }
  /* Стили overlay меню */
  .menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    z-index: 1000;
    display: none;
    padding-top: 111px;
    margin-top: 0;
    overflow-y: auto;
  }
  .menu-list {
    display: flex;
    flex-direction: column;
    gap: 25px;
    list-style: none;
    padding: 0;
  }
  .menu-list li {
    font-size: 20px;
    line-height: 120%;
    font-weight: 300;
  }
  .menu-list a {
    text-decoration: none;
    color: #000;
    font-size: 20px;
    line-height: 120%;
    font-weight: 300;
  }
  /* Состояние открытого меню */
  .menu-open .header-content {
    align-items: flex-start;
    justify-content: space-between;
  }
  .menu-open .header-logo {
    position: static;
    transform: none;
    margin-left: auto;
    order: 1;
  }
  .menu-open .menu-toggle {
    order: 2;
    margin-left: auto;
    margin-right: 0;
  }
  .menu-open .burger-icon {
    display: none;
  }
  .menu-open .close-icon {
    display: block;
  }
  .menu-open .menu-overlay {
    display: block;
  }
  .footer {
    margin-top: 82px;
    padding: 38px 0 32px;
  }
  .footer .container {
    gap: 66px;
    flex-direction: column;
  }
  .footer img {
    display: none;
  }
  .footer-wrapper {
    display: block;
  }
  .footer-desc {
    margin-top: 0;
    font-size: 12px;
    width: 100%;
  }
  .footer-list {
    display: none;
  }
  .footer-images {
    gap: 31px;
    margin-top: 0;
  }
  .footer-images a svg {
    height: 27px;
  }
  .footer-images a:first-child svg {
    width: 27px;
  }
  .footer-images a:nth-child(2) svg {
    width: 38px;
  }
  .footer-images a:nth-child(3) svg {
    width: 27px;
  }
  .footer-images a:nth-child(4) svg {
    width: 26px;
  }
  .footer-images a:nth-child(5) svg {
    width: 30px;
  }
  .footer-images a:nth-child(6) svg {
    width: 23px;
  }
}
